#logo-description {
    margin: 20px 0 25px 0;
    font-size: 32px;
    font-weight: 500;
    background-image: linear-gradient(90deg, #2537B0, #7EE6CE);
    background-size: 100%;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    margin-right: 5px;
}

.logo {
    height: 128px;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 20px;
}

@media only screen and (max-width: 480px) {
    .logo {
        height: 96px;
        background-size: 75%;
        margin-bottom: 5px;
    }
    #logo-description {
        font-size: 20px;
    }
    .login {
        padding-bottom: 15px;
    }
}