#root {
  height: 100vh;
}

.App {
  min-height: 100vh;
  min-width: 100%;
  /* overflow-y: scroll; */
  /* overflow-x: hidden; */
}

.App-inner {
  position: relative;
  text-align: center;
  padding: 2em 2em;
  top: 0;
  left: 50vw;
  max-width: 720px;
  transform: translate(-50%, 0);
  max-height: 100%;
}

.task-display .image-gallery {
  width: 100%;
}

@media only screen and (max-width: 768px) {
  .App-inner {
    overflow-y: scroll;
    max-width: 100vh;
  }
  .task-display .image-gallery {
    width: 100%;
  }
  .image-gallery-slide img {
    height: 144px !important;
  }
  .image-gallery-thumbnail {
    width: 52px;
  }
  .image-gallery-thumbnail img.image-gallery-thumbnail-image {
    width: 48px !important;
  }
}

.App-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(#ffffffff, #ffffff80);
}

.login {
  padding-bottom: 5em;
}

.login .header {
  margin-bottom: 20px;
  font-size: 2.5em;
}

.input-inner-header {
  padding-left: 10px;
  padding-right: 10px;
  border-right: 1px solid lightgray;
}

.login-btns {
  margin-top: 1em;
}

.login-btns .wbtn {
  color: black;
  background: white;
  transition: all .25s;
  border-radius: 0;
  margin: 0.25em 0.75em;
  width: 10em;
}
.login-btns .wbtn:hover {
  color: white;
}

.login-btns .login-btn {
  border-color: #531dab;
}

.login-btns .login-btn:hover {
  background: #531dab;
  border-color: #531dab;
}

.login-btns .history-display-btn {
  background: white;
  border-color: #3f6600;
}

.login-btns .history-display-btn:hover {
  background: #3f6600;
  border-color: #3f6600;
}

.login-btns .lang-btn {
  border-color: #096dd9;
}

.login-btns .lang-btn:hover {
  background: #096dd9;
  border-color: #096dd9;
}

.login-btns .github-btn {
  border-color: #262626;
}

.login-btns .github-btn:hover {
  background: #262626;
  border-color: #262626;
}

.login-description {
  margin-top: 5px;
}


.login-input {
  margin-top: 5px;
}

.login .enter-btn {
  margin-left: 10px;
}

.login .enter-btn:hover {
  cursor: pointer;
}

.image-gallery-slide img {
  width: 100%;
  height: 256px;
}

.score-board .header {
  margin-bottom: 20px;
  font-size: 1.75em;
}
.score-board .header .header {
  margin-top: 5px;
}

.score-board .user-score {
  color: darkgreen;
}

.poetry-container {
  position: relative;
  height: calc(100vh - 80px);
  width: 100%;
  margin: 0;
}

@media only screen and (min-width: 720px) {
  .poetry-container {
    width: calc(100% - 6em);
    margin: 0 3em;
  }
}

.poetry-inner {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 100%;
  padding: 1em 0;
}

.poetry-card {
  margin: 0.5em;
  height: calc(50% - 1.5em);
  border-radius: 0.5em;
  border: lightgray dashed 0.1em;
  background: #ffffffA0;
}

.poetry-card.selected {
  border: slateblue solid 0.2em;
  background: #ffffffe0;
}

.poetry-card .title {
  font-size: 1.5em;
  margin-bottom: 0.25em;
}
.poetry-card .author {
  font-size: 1.15em;
  margin: 0 0 0.5em 0;
}
.poetry-card .line {
  font-size: 1.1em;
}

.turing-test .submit-btn {
  position: absolute;
  right: 1em;
  bottom: 1em;
  transform: translateY(-100%);
  z-index: 1000;
}

.submit-btn-input {
  opacity: 0.5;
}

.submit-btn-input.last {
  opacity: 1;
}

.poetry-card-inner {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.description {
  background: #ffffffa0;
  position: absolute;
  top: calc(100% - 5em);
  left: 0;
  margin: 0.5em 1.5em;
  border-radius: 0.5em;
  padding: 0.5em 1em;
  font-size: 0.9em;
  text-align: left;
}

.timer {
  position: fixed;
  right: 1em;
  top: 1em;
  z-index: 3000;
}

.guide-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #00000080;
  color: white;
  font-size: 1.25em;
  z-index: 2000;
}

.guide-container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  padding: 1em;
  text-align: center;
}

.guide-container .skip {
  margin-top: 0.75em;
}

.guide-container .btn {
  margin: 0 0.5em;
}

.loading-mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #ffffffa0;
}

.loading-mask .mask-inner {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 2.5em;
}

.email-input {
  position: relative;
  margin-top: 1em;
}

.rank-board {
  width: 100%;
  height: 100%;
  padding: 2.5em 1.5em;
  text-align: center;
  overflow: scroll;
}

.rank-board .top-info {
  font-size: 2em;
  width: 100%;
}

.rank-board .header-info {
  margin: 1em 0;
  font-size: 1.25em;
}

.rank-board .rank-table {
  border: 1px lightslategrey solid;
}

.rank-board .rank-table .skip {
  border: 1px lightslategrey solid;
}

.rank-board .rank-table .row-container {
  border: 1px lightslategrey solid;
}

.rank-board .rank-table .row-container .rank {
  width: 2em;
  border-right: 1px lightslategrey solid;
}

.poetry-header {
  position: relative;
  height: 80px;
  top: 0;
  width: 100%;
  transform: translate(0, 0);
  padding: 30px 0 0 0;
}

.poetry-header .tail {
  text-align: right;
  font-size: 0.5em;
  color: #808080;
  margin-right: 15px;
}

.prompt {
  font-size: 14px;
  padding: 10px 15px;
  background: #ffefa780;
  border-radius: 5px;
  margin-bottom: 10px;
}

.prompt p {
  margin: 4px 0;
  padding: 0;
}